.taskId,
.title,
.description,
.status {
  font-family: "Lato", sans-serif; 
}

.taskId {
  font-size: 14px;
  color: #1f2937;
}

.title {
  font-size: 22px;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 8px;
  text-align: left;
}

.description {
  font-size: 16px;
  color: #1f2937;
  margin-bottom: 10px;
}

.status {
  font-size: 16px;
  color: #1f2937;
}

.statusCompleted {
  color: #1f2937; 
}

.statusPending {
  color: #1f2937; 
}

.updateButton,
.deleteButton,
.cancelButton {
  margin: 5px;
  padding: 8px 16px;
  color: #1f2937;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  border: 1px solid #c2cbd5;
}

.updateButton {
  background-color: #ffffff; 
}

.deleteButton {
  background-color: #ffffff; 
  border: 1px solid #c2cbd5;
}



.cancelButton {
  background-color: #ffffff; 
  border: 1px solid #c2cbd5;
}

.cancelButton:hover {
  background-color: #1f2937;
  color: #ffffff;
 
}

.updateButton:hover {
  background-color: #1f2937; 
  color: #ffffff;
}

.deleteButton:hover {
  background-color: #1f2937;
  color: #ffffff;
}
