/* User Info Container */
.userInfoContainer {
  max-width: 600px;
  margin: 60px auto;
  padding: 30px;
  border: 1px solid #c2cbd5;
  border-radius: 12px;
  font-family: 'Lato', sans-serif;
  background: #ffffff;
  color: #1f2937;
}

/* User Info Title */
.userInfoTitle {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
}

/* User Info Detail */
.userInfoDetail {
  font-size: 18px;
  margin: 12px 0;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
}

.userInfoDetail:last-child {
  border-bottom: none;
}

.userInfoContainer p span {
  font-weight: bold;
}

/* Redirect Text */
.redirectText {
  font-size: 16px;
  text-align: center;
  margin-top: 20px;
}


.changePassLink{
  color: #007bff;
  text-decoration: none;
}