.pnfContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.pnfMessage {
  text-align: center;
  background-color: #ffffff;
  color: #1f2937;
  padding: 2rem 3rem;
}

.pnfMessage h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.pnfMessage p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.pnfBtn {
  padding: 10px 20px;
  background: #ffffff;
  border: 1px solid #c2cbd5;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pnfBtn:hover {
  background: #1f2937;
  color: #ffffff; /* Dark text color */
}
