/* HomePage.module.css */
.homeContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
}

.homeContainer .contentWrapper {
  max-width: 1200px;
  width: 100%;
  margin-top: 20px;
}

/* Navbar container */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background-color: #ffffff; /* Dark gray background */
  border: 1px solid #c2cbd5;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Light shadow */
  border-radius: 8px; /* Rounded corners */
}

/* Logo */
.logo a {
  text-decoration: none;
  color: #646363;
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 1px;
  transition: color 0.3s;
  margin-right: 0.5em;
}

.logo a:hover {
  color: #fbbf24; /* Tailwind's yellow for hover effect */
}

/* Navigation Links */
.navList {
  display: flex;
  list-style: none;
  gap: 1.5rem;
}

.navlinks {
  text-decoration: none;
  color: #646363;
  transition: color 0.3s, transform 0.2s;
}

/* Active State for Navigation Links */
.navlinks.active {
  color: #fbbf24; /* Active color (same as hover) */
  font-weight: bold; /* Make it bold for emphasis */
}

.navlinks:hover {
  color: #fbbf24; /* Hover color for links */
  transform: translateY(-2px); /* Subtle lift effect */
}

/* Logout Button */
.navList button {
  background: none;
  border: none;
  color: #646363;
  cursor: pointer;
  transition: color 0.3s, transform 0.2s;
}

.navList button:hover {
  color: #fbbf24;
  font-weight: bold;
  transform: translateY(-2px); /* Same effect as nav links */
}

/* Hamburger Button (Mobile) */
.hamburger {
  display: none; /* Hidden by default */
  font-size: 1.5rem;
  background: none;
  border: none;
  color: #646363;
  cursor: pointer;
  transition: transform 0.3s ease;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .hamburger {
    display: block; /* Show hamburger button only on mobile */
  }

  .navList {
    position: fixed;
    top: 0;
    left: -100%; /* Hide menu by default */
    width: 50%; /* Full width of the screen */
    height: 100%; /* Full height of the screen */
    background: #ffffff; /* Same background color */
    flex-direction: column;
    padding: 2rem;
    /* transition: left 0.3s ease-in-out; */
    margin-top: 0; /* Ensure no overlap at the top */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    border: 1px solid #c2cbd5;
  }

  .showMenu {
    left: 0; /* Slide in the menu when active */

  }

  .navList li {
    margin-bottom: 1.25rem; /* Space between each item */
  }

  .navlinks {
    font-size: 1.25rem; /* Increase font size for mobile */
  }

  /* Active hamburger button color */
  .hamburger.active {
    color: #fbbf24;
  }
}
