.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7); /* Darker overlay for better contrast */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999; /* Ensures the modal is always on top */
  transition: opacity 0.3s ease-in-out; /* Smooth fade-in effect */
}

.modal {
  background: #fff;
  padding: 25px;
  border-radius: 12px; /* Rounded corners for a softer look */
  width: 320px;
  max-width: 90%;
  text-align: center;
  position: relative;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Light shadow for depth */
  transition: transform 0.3s ease-in-out; /* Smooth scaling effect on open */
}

.modal.show {
  transform: scale(1.05); /* Slight zoom effect on modal open */
}

.content {
  margin-top: 15px;
  font-size: 16px;
  color: #555; /* Softer gray text */
}

.modal h2 {
  font-size: 18px;
  color: #333;
  margin-bottom: 15px;
}

.modal button {
  padding: 10px 20px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
  transition: background-color 0.3s ease, transform 0.2s ease;
  border: 1px solid #c2cbd5;
}


