.changePasswordContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f4f4f9;
}

.changePasswordForm {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.changePasswordForm h2 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
  color: #646363;
}

.inputGroup {
  margin-bottom: 15px;
}

.inputGroup label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #2f3964
}

.inputGroup input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.changePasswordBtn {
  width: 100%;
  padding: 12px;
  background-color: #5f74d1;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.changePasswordBtn:hover {
  background-color: #394eb0;
}

.redirectText {
  text-align: center;
  margin-top: 10px;
}

.homeLink {
  color: #007bff;
  text-decoration: none;
}

.homeLink:hover {
  text-decoration: underline;
}
