/* SignUp.module.css */

.signupWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  background-color: #f8f9fa; /* Light background for contrast */
}

.signupContainer {
  width: 90%;
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.signupForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.signupForm h2 {
  color: #646363;
}

.message {
  padding: 8px;
  margin: 5px 0px;
}

.inputGroup {
  width: 100%;
  margin-bottom: 15px;
  text-align: left;
}

.inputGroup label {
  font-weight: 500;
  margin-bottom: 5px;
  display: block;
  color: #2f3964;
}

.inputGroup input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

/* Error message styling */
.errorMessage {
  color: red;
  font-size: 12px;
  margin-top: 5px;
}

/* Password wrapper for show/hide button */
.passwordWrapper {
  position: relative;
}

.passwordWrapper input {
  padding-right: 40px; /* Make space for the toggle button */
}

.passwordToggleBtn {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
}

/* Button styles */
.signupBtn {
  width: 100%;
  padding: 12px;
  background-color: #5f74d1;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 500;
  transition: background 0.3s ease;
}

.signupBtn:hover {
  background-color: #394eb0;
}

/* Redirect text and link */
.redirectText {
  margin-top: 15px;
  font-size: 14px;
}

.loginLink {
  color: #007bff;
  font-weight: 500;
  text-decoration: none;
}

.loginLink:hover {
  text-decoration: none;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .signupContainer {
    padding: 15px;
  }

  .signupBtn {
    padding: 10px;
  }
}
