/* homeContainer should fill the entire screen */
.homeContainer {
  display: flex;
  flex-direction: column;
  min-height: 100vh;  /* Ensure full height of the screen */
}

/* The contentWrapper should take up the remaining space */
.contentWrapper {
  flex: 1;  /* Makes sure the content wrapper fills up the space between Navbar and Footer */
}

/* Ensuring Footer stays at the bottom */
footer {
  margin-top: auto;  /* Pushes the footer to the bottom of the screen */
}
