.resetContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f4f4f9;
}

.resetForm {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  border: 1px solid #c2cbd5;

}

.resetForm h2 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
  color: #646363;
}

.resetInputGroup {
  margin-bottom: 15px;
}

.resetInputGroup label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #2f3964;
}

.resetInputGroup input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.resetBtn {
  width: 100%;
  padding: 12px;
  background-color: #5f74d1;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.resetBtn:hover {
  background-color: #394eb0;
}



/* .errorMessage for error styling */
.errorMessage {
  color: red;
  font-size: 12px;
}

/* Password wrapper to position the show/hide button */
.passwordWrapper {
  position: relative;
}

.passwordToggleBtn {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
}
