/* Wrapper to center the login container */
.loginWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  background-color: #f8f9fa; /* Light gray background */
}

/* Main login container */
.loginContainer {
  width: 90%;
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Login form styling */
.loginForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.loginForm h2 {
  color: #646363;
}

.message{
  /* background: red; */
  padding: 8px;
  margin: 4px 0px;
}

.inputGroup {
  width: 100%;
  margin-bottom: 15px;
  text-align: left;
}

.inputGroup label {
  font-weight: 500;
  margin-bottom: 5px;
  display: block;
  color: #2f3964;
}

.inputGroup input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

/* Button styling */
.loginBtn {
  width: 100%;
  padding: 12px;
  background-color: #5f74d1;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 500;
  transition: background .5s ease-in-out;
}

.loginBtn:hover {
  background-color: #394eb0;
}


/* Redirect text and links */
.redirectText {
  margin-top: 15px;
  font-size: 14px;
  color: #646363;
}

.signupLink {
  color: #007bff;
  font-weight: 500;
  text-decoration: none;
}

.signupLink:hover {
  text-decoration: underline;
}

/* Sign-up div styling */
.signUpDiv {
  margin-top: 20px;
  text-align: center;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .loginContainer {
    padding: 15px;
  }

  .loginBtn {
    padding: 10px;
  }
}


.errorMessage {
  color: red;
  font-size: 12px;
}

.passwordWrapper {
  position: relative;
}

.passwordToggleBtn {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
}
