/* Wrapper to center the form */
.forgotContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f8f9fa;
}

/* Forgot password form container */
.forgotForm {
  width: 90%;
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.forgotForm h2 {
  color: #646363;
}

/* Input group */
.forgotInputGroup {
  width: 100%;
  margin-bottom: 15px;
  text-align: left;
}

.forgotInputGroup label {
  font-weight: 500;
  margin-bottom: 5px;
  display: block;
  color: #2f3964;
}

.forgotInputGroup input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

/* Submit button */
.forgotBtn {
  width: 100%;
  padding: 12px;
  background-color: #5f74d1;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 500;
  transition: background 0.3s ease;
}

.forgotBtn:hover {
  background-color: #394eb0;
}

/* Redirect text */
.forgotRedirectText {
  margin-top: 15px;
  font-size: 14px;
}

.forgotLink {
  color: #007bff;
  font-weight: 500;
  text-decoration: none;
}

.forgotLink:hover {
  text-decoration: none;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .forgotForm {
    padding: 15px;
  }

  .forgotBtn {
    padding: 10px;
  }
}
