

body,
html {
  overflow-x: hidden; /* Prevent horizontal overflow */
}

.foot {
  background-color: #ffffff; /* Dark gray background */
  border: 1px solid #c2cbd5;
  background: #ffffff;
  color: #646363;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  width: 100%; /* Ensure the width is set to 100% */
}

.social {
  text-align: center;
  font-size: 1.6rem;
  margin: 10px 0;
}

.line {
  content: "";
  display: block;
  border: 1px solid #c2cbd5;
  width: 80%;
  max-width: 1280px;
  margin: 20px auto;
}

.copy {
  background: #ffffff;
  text-align: center;
  padding: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
